.App .login{
    background: #f8f8f8;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;

    .login__container{
        padding: 100px;
        text-align: center;
        background-color: white;
        border-radius: 10px;
        box-shadow: 0 1px 3px rbga(0,0,0,0.12);

        img{
            object-fit: contain;
            height: 100px;
            margin-bottom: 40px;
        }

        button{
            margin-top: 40px;
            text-transform: inherit!important;
            background: #0a8d48;
            color: white;
        }
    }
}