.App .invite__container {
    background: #f8f8f8;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;

    .invite__box {
        padding: 30px;
        text-align: center;
        background-color: white;
        border-radius: 10px;
        box-shadow: 0 1px 3px rbga(0, 0, 0, 0.12);
        text-align: center;
        max-width: 100%;
        width: calc(min(350px,100%));
        
        .invite__img {
            width: 123px;
        }

        .invite__text {
            p {
                font-size: 19px;
                color: #6d6d6d;
            }

            h3{
                padding: 5px 0 25px 0;
                font-size: 20px;
            }
        }

        .accept__invite__btn {
            width: 100%;
            background: #009688;
            color: #fff;
        }
    }
}
